import React from "react"
import styled from "styled-components"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

import Layout from "../../components/layout"
import SEO from "../../components/seo"
import { theme } from "../../theme/GlobalStyles"
import ProjectNav from "../../components/projectnav"
import ProjectText from "../../components/projecttext"

const Img1 = styled(Img)`
    width: 45%;
    display: inline-block;
    margin: 1%;
    @media (max-width: ${theme.mobile}) {
        width: 80vw;
    }
`
const Img2 = styled(Img)`
    width: 45%;  
    display: inline-block;
    margin: 1%;
    @media (max-width: ${theme.mobile}) {
        width: 80vw;
    }  
`
const CovidPage = () => {
    const data = useStaticQuery(graphql`
        query {
            img1: file(relativePath: { eq: "covid/graph1.png" }) {
                ...image
            }
            img2: file(relativePath: { eq: "covid/graph2.png" }) {
                ...image
            }
        }
    `)

  return (
    <Layout>
      <SEO title="COVID-19 Analysis" />
      <ProjectNav active="COVID-19 Analysis">
          <ProjectText
            type="REGRESSION MODELING"
            title="COVID-19 ANALYSIS"
            tools="PYTHON"
            body1="I analyzed the data from the Johns Hopkins University Center for Systems Science and Engineering to predict the number of cases in the coming months."
            body2="To do this, I used multiple techniques to create fitting predictions, including auto-regressive models and linear regression with polynomial basis functions to map out my predictions. I was then able to draw conclusions by examining the models; for example, by comparing the beta coefficients and root mean square errors, I was able to explain why the US’s exponential growth rate was higher than Italy’s.
            "
            link="Visit Github"
            url="https://github.com/jadechen170/CLA/tree/master/covid-19-analysis">
          </ProjectText>
          
          <Img1 fluid={data.img1.childImageSharp.fluid} />
          <Img2 fluid={data.img2.childImageSharp.fluid} />
      </ProjectNav>
    </Layout>
  )
}
export default CovidPage